@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/static/pretendard-dynamic-subset.css");

:root {
	--app-width: 360px;
	--half-unit: 2px;
	--standard-unit: 4px;
	--double-unit: 8px;
	--triple-unit: 12px;
	--quadruple-unit: 16px;
	--quintuple-unit: 20px;
	--sextuple-unit: 24px;
	--octuple-unit: 32px;
	--box-shadow: 2px 2px 8px 4px rgb(0, 0, 0, 0.04);
	--main-orange: #E05E21;
	--main-yellow: #FFDC00;
	--charcoal: #3C3C3C;
	--dim-gray: #646464;
	--gray: #7A7A7A;
	--silver: #A8A8A8;
	--light-gray: #D2D2D2;
	--gainsboro: #E4E4E4;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video,
textarea {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	box-sizing: border-box;
}
a {
	color: inherit;
	text-decoration: none;
}
button {
	padding: 0;
	cursor: pointer;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body * {
	font-family: 'Pretendard';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
ol, ul, li {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

::-webkit-scrollbar {
	width: 0;
}
::placeholder {
	color: var(--silver);
}

#root {
	position: relative;
	width: var(--app-width);
	height: 100vh;
	margin: 0 auto;
    font-size: 14px;
}

.bottomsheet {
	position: fixed;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    padding: var(--sextuple-unit) var(--sextuple-unit) 40px;
    width: var(--app-width);
    background: #fff;
    border-radius: 32px 32px 0px 0px;
    z-index: 100;
    transition: max-height 0.1s ease-out;
}
.hide {
	padding: 0;
	max-height: 0;
	overflow: hidden;
}
.show {
	max-height: 100vh;
}
.overlap::before {
	content: "";
	position: absolute;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
	left: 0;
    background: rgba(0, 0, 0, 0.3);
	z-index: 1;
}
.unscrollable {
	overflow: hidden;
}
